import { defineComponent } from 'vue';
import I18n from "../../../../../../utils/i18n";
import HueTable from "../../../../../../components/HueTable.vue";
import { generateValueColumnKey } from "./CounterSet";
import VarianceCell from "./VarianceCell.vue";
