var NAME_COLUMNS = [{
  label: I18n('Group Name'),
  key: 'groupName'
}, {
  label: I18n('Counter Name'),
  key: 'counterName'
}];
export default defineComponent({
  components: {
    HueTable: HueTable,
    VarianceCell: VarianceCell
